@import "../../vars.scss";

.signUp {
    width: 100%;
    height: 90vh;
    background: white;

    display: flex;
    justify-content: center;
    align-items: center;
    .signUpForm {
        background: $tertiary-color;
        width: 50%;
        height: 60%;
        padding: 3rem 0;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        font-size: 2rem;
        box-shadow: 0 0px 2px $secondary-color, 0 0px 2px $secondary-color;
        padding: 2.5% 7%;
        
        .label {
            font-size: 2rem;
            width: 100%;
            margin: 2rem 0;
            padding-right: 20%;
            display: flex;
            justify-content: space-between;
            align-items: flex-end;

            .input {
                border: none;
                background: none;
                border-bottom: 2px solid $border-color;
                width: 70%;
                font-size: 2rem;

            }
        }
    }
}
